.articles{justify-content:space-between;margin:0 30px}.articleCard,.articles{display:flex;flex-wrap:wrap}.articleCard{-moz-box-shadow:0 3px 36px 0 hsla(0,0%,45.9%,.26);-webkit-box-shadow:0 3px 36px 0 hsla(0,0%,45.9%,.26);align-content:stretch;align-items:stretch;background-color:#fff;border-radius:24px;box-shadow:0 3px 36px 0 hsla(0,0%,45.9%,.26);flex-direction:column;margin:15px;min-height:400px;overflow:hidden;position:relative;transition:all .75s}.articleCard:hover{box-shadow:0 3px 36px 0 hsla(0,0%,45.9%,.86);transform:translateY(-1px);transition:all .75}.articleCard:nth-child(2n){align-self:flex-end}.articleCardImage{background-color:#f321d7;background-position:50%;background-size:cover;border-bottom:0 solid #000;margin-bottom:7.5px;overflow:hidden}.articleCard:nth-child(2n) .articleCardImage{order:2}.articleExcerpt{font-size:16px;font-weight:400;line-height:1.25;margin-bottom:7.5px;padding:0 30px}